const eventGallery = document.querySelector('.gallery');

const imgHtmlAdd = images
    .map(
        ({ preview, original, description }) =>
            `<li class="gallery-item">
                <a class="gallery-link" href="${original}">
                    <img
                        class="gallery-image"
                        src="${preview}"
                        alt="${description}"
                    />
                </a>
            </li>`
    )
    .join('');

eventGallery.innerHTML = imgHtmlAdd;

const lightbox = new SimpleLightbox('.gallery a', {
    captions: true,
    captionSelector: 'img',
    captionsData: 'alt',
    captionDelay: 250,
});

lightbox.refresh();
